.loading_container {
    /* position: fixed; */
    width: 100vw;
    height: 100vh;
    /* top: 0;
    bottom: 0;
    left: 0;
    right: 0; */
    z-index: 99;
    position: relative;

    overflow: hidden;
}
.loading_container::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.loading_container .bg_wrap {
    position: relative;
    width: 100%;
    height: 100%;
    aspect-ratio: 1/2;
}
.loading_container .bg_wrap>img:nth-child(1){
    position: absolute;
    top: 0;
    left: 0;
    width: 25%;
}
.loading_container .bg_wrap>img:nth-child(2){
    position: absolute;
    bottom: 0;
    right: 0;
    width: 25%;
}


/* svg - ë¡œë”© ì• ë‹ˆë©”ì´ì…˜ ë°•ìŠ¤ */
.loading_container .svg_wrap{
    position: absolute;
    top: 50%;
    left: 50%;
    aspect-ratio: 580/190;
    transform: translate(-50%, -50%) scale(1.5);
    animation: fadeupAni .5s ease-out forwards;
}
@keyframes fadeupAni {
    0%{
        transform: translate(-50%, 0%) scale(1.5);
        opacity: 0;
    }
    100%{
        transform: translate(-50%, -50%) scale(1.5);
        opacity: 1;
    }
}

.loading_container .svg_wrap>svg{
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
}
.loading_container #text{
    transform: translateY(70%);
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;
    font-size: 161.047px;
}
.loading_container .water-fill {
    animation: wave 2s infinite linear, fill-up 2s infinite ease-out alternate;
}

/* í‚¤í”„ë ˆìž„ ì• ë‹ˆë©”ì´ì…˜ */
@keyframes wave {
    0% {
        x: -400px;
    }
    100% {
        x: 0;
    }
}
@keyframes fill-up {
    0% {
        height: 0;
        y: 130px;
    }
    100% {
        height: 160px;
        y: -30px;
    }
}