.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    width: 100%;
    height: 100%;

    background: url(../../../public/images/age_bg.png) no-repeat center/cover;
}

.ReactModal__Overlay {
    opacity: 0;
}

.ReactModal__Overlay--after-open{
    opacity: 1;
}

.ReactModal__Overlay--before-close{
    opacity: 0;
    transition: opacity .5s ease-in-out;
}


